.home__body{
  text-align: center;
  box-sizing: border-box;
  font-family: 'VT323', monospace;
  color:#333;
  letter-spacing: 1.5px;
  line-height: 1.75;
  width: 90vw;
  max-width: 1170px;
  margin: 5rem auto;
  display: grid;
  grid-gap: 2rem;
  gap: 2rem;
}

.home__body  h3 {
  font-size:32px;
}
@media screen and (min-width:480px) {
  .home__body {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    text-align: center;
    justify-content: center;
    width:100%;
    
    /* align-items: start; */
  }
}
@media screen and (min-width: 1024px) {
  .home__body {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    text-align: center;
    justify-content: center;
     width:100%;
    /* align-items: start; */
  }
}

