}

a{
  text-decoration: none;
}

.booklist {
  width: 90vw;
  max-width: 1170px;
  margin: 5rem auto;
  display: grid;
  grid-gap: 2rem;
  gap: 2rem;
}

.booklist__title{
  background-color: red;
  display: none;
}
@media screen and (min-width:480px) {
  .booklist {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    text-align: center;
    justify-content: center;
    width:100%;
    
