
   
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background: #f1f5f8;
  color: #222;
}

.all__news {
  width: 90vw;
  max-width: 1170px;
  margin: 5rem auto;
  display: grid;
  gap: 2rem;
  text-align: center;
  justify-content: center;
}

@media screen and (min-width:480px) {
  .all__news {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    text-align: center;
    justify-content: center;
    width:100%;
    align-items: start;
  }
}
@media screen and (min-width: 1024px) {
  .all__news {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    text-align: center;
    justify-content: center;
     width:100%;
    align-items: start;
  }
}

.all__news {
  background: #fff;
  border-radius: 1rem;
  padding: 1rem 2rem;
} 

.news__title {
  color: #617d98;
  margin-top: 0.25rem;
}


